 from 'axios'\n  import VueAxios from 'vue-axios'\n  import {\n    Button,\n    Card,\n    Form,\n    FormItem,\n    Input\n  } from 'element-ui'\n\n  Vue.use(VueAxios, axios);\n  Vue.use(Button);\n  Vue.use(Card);\n  Vue.use(Form);\n  Vue.use(FormItem);\n  Vue.use(Input);\n\n  export default {\n    name: \"SignIn\",\n\n    data() {\n\n      return {\n        rules: {\n          email: [\n            { required: true, trigger: 'blur'}\n          ],\n          password: [\n            { required: true, trigger: 'blur' }\n          ]\n        },\n\n        signinForm: {\n          email: '',\n          password: ''\n        },\n      }\n    },\n    methods: {\n      submitForm(resetFormPassword) {\n        this.$refs[resetFormPassword].validate((valid) => {\n          if (valid) {\n            alert('submit!');\n            this.$router.push({path: '/resetPasswordDone'});\n          } else {\n            console.log('error submit!!');\n            return false;\n          }\n        });\n      },\n    }\n  }\n</script>\n<style scoped>\n  .box-card {\n    max-width: 30%;\n    margin: 0 auto;\n    margin-bottom: 15%;\n  }\n\n  #signin {\n    padding-top: 10%;\n    width: 100%;\n    height: 100%;\n    background-repeat: repeat;\n    background: -webkit-linear-gradient(70deg, #ff6464 40%, #5ac8fa 40%);\n    background: -o-linear-gradient(70deg, #ff6464 40%, #5ac8fa 40%);\n    background: -moz-linear-gradient(70deg, #ff6464 40%, #5ac8fa 40%);\n    background: linear-gradient(70deg, #ff6464 40%, #5ac8fa 40%);\n  }\n\n  .title {\n    width: 50%;\n    margin: 0 auto;\n    text-align: center;\n  }\n  .content {\n    font-size: 16px;\n  }\n  .list {\n    font-size: 13px;\n  }\n\n</style>"],"sourceRoot":""}]);

